<template>
  <label class="v-check-container" :class="checkClasses" @click="checkClick">
    <input class="hidden-input v-check-input" type="checkbox" :value="value" v-model="model" :disabled="disabled">
    <div class="v-check-handler">
      <i class="v-check-checked-icon icon-checkmark"></i>
      <i class="v-check-indeterminate-icon icon-minus"></i>
    </div><div class="v-check-label"><slot></slot></div>
  </label>
</template>

<script>
export default {
  model: {
    prop: 'checked',
    event: 'change'
  },
  props: {
    checked: {
      type: [Array, Boolean]
    },
    value: {
      required: false,
      type: [String, Number, Boolean],
      default: ''
    },
    indeterminate: {
      required: false,
      type: Boolean,
      default: false
    },
    disabled: {
      required: false,
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      disabledClassName: 'disabled',
      pickedClassName: 'checked',
      indeterminateClassName: 'indeterminate'
    }
  },
  computed: {
    checkClasses () {
      return {
        [this.disabledClassName]: this.disabled,
        [this.pickedClassName]: typeof this.model === 'boolean' ? this.model : this.model.indexOf(this.value) !== -1,
        [this.indeterminateClassName]: this.indeterminate
      }
    },
    model: {
      set (val) {
        this.$emit('change', val)
        this.onChange(val)
      },
      get () {
        return this.checked
      }
    }
  },
  methods: {
    checkClick (event) {
      if (this.disabled) {
        event.stopPropagation()
        event.preventDefault()
        return false
      }
    },
    onChange (val) {
      if (typeof this.checked === 'boolean') {
        this.$emit('on-change', val)
      }
    }
  }
}
</script>
<style src="./vCheck.less" lang="less"></style>



