<template>
  <div class="calendar-picker-container">
    <div>
      <v-button class="quick-select-btn" :class="{ 'indeterminate': monthBtnState === 1 }" :type="monthBtnState === 2 ? 'primary' : 'ghost'" size="mini" @click.native="clickMonthQuickSelect">{{ $t('time.currentMonth') }}</v-button>
      <v-button class="quick-select-btn" type="ghost" size="mini" @click.native="clickInverseMonthQuickSelect">{{ `${$t('text.inverseSelect')}（${$t('time.currentMonth')}）` }}</v-button>
    </div>
    <calendar :locale="locale" :current-time="currentTime" :selected="selected" @yearChange="onYearChange" @monthChange="onMonthChange" @selectChange="selectChange"></calendar>
    <div class="quick-select-container" :class="['quick-' + quickType]">
      <div class="quick-select-title">
        <span>
          {{ $t('text.quickSelect') + '（' + year + '）' }}
        </span>
        <v-radio-group>
          <v-radio v-for="type in quickTypes" :key="type" type="button" size="mini" :value="type" v-model="quickType" name="quickType">{{ $t('time.every' + type) }}</v-radio>
        </v-radio-group>
      </div>
      <div class="quick-week-container">
        <v-button v-for="(day, key) in dayMap" :key="day" class="quick-select-btn" :class="{ 'indeterminate': weekBtnState[day] === 1 }" :type="weekBtnState[day] === 2 ? 'primary' : 'ghost'" size="mini" @click.native="clickWeekQuickSelect(key)">{{ $t('time.' + day) }}</v-button>
      </div>
      <div class="quick-date-container">
        <v-button v-for="(n, index) in 31" :key="n" class="quick-select-btn" :class="{ 'indeterminate': dateBtnState[index + 1] === 1 }" :type="dateBtnState[index + 1] === 2 ? 'primary' : 'ghost'" size="mini" @click.native="clickDateQuickSelect(index + 1)">{{ index + 1 }}</v-button>
      </div>
    </div>
  </div>
</template>

<script>
// import { unformatcalendarData, formatcalendarData } from './assets/utils'
import moment from 'moment'
import { modified } from './assets/moment-locale-zh-cn'

export default {
  name: 'calendarPicker',
  props: {
    locale: {
      required: false,
      type: String,
      default: 'zh-cn'
    },
    currentTime: {
      required: false,
      type: [Number, String],
      default: (+new Date())
    },
    selected: {
      required: false,
      type: Object,
      default: null
    }
  },
  data () {
    return {
      year: null,
      month: null,
      quickType: 'Week',
      quickTypes: ['Week', 'Month'],
      selectedValue: 1,
      unSelectedValue: 0,
      weekBtnState: {
        'Sundays': 0,
        'Mondays': 0,
        'Tuesdays': 0,
        'Wednesdays': 0,
        'Thurdays': 0,
        'Fridays': 0,
        'Saturdays': 0
      },
      monthBtnState: 0,
      dayMap: {
        0: 'Sundays',
        1: 'Mondays',
        2: 'Tuesdays',
        3: 'Wednesdays',
        4: 'Thurdays',
        5: 'Fridays',
        6: 'Saturdays'
      },
      Mondays: {
        total: 0,
        selected: 0
      },
      Tuesdays: {
        total: 0,
        selected: 0
      },
      Wednesdays: {
        total: 0,
        selected: 0
      },
      Thurdays: {
        total: 0,
        selected: 0
      },
      Fridays: {
        total: 0,
        selected: 0
      },
      Saturdays: {
        total: 0,
        selected: 0
      },
      Sundays: {
        total: 0,
        selected: 0
      },
      monthState: {
        total: 0,
        selected: 0,
        startIndex: 0,
        endIndex: 0
      },
      dateState: {},
      datesInMonth: [31, 28, 31, 30, 31, 30, 31, 31, 30, 31, 30, 31],
      dateBtnState: {}
    }
  },
  created () {
    this.initPlugins()
  },
  mounted () {
  },
  computed: {
  },
  methods: {
    initPlugins () {
      moment.locale(this.locale)
      moment.updateLocale(this.locale, modified)
    },
    onYearChange (year) {
      this.year = year
      var selectedDays = this.selected[year] || []
      var time = moment().year(year)
      this.resetWeekState()
      this.initDateState(year)
      var firstDay = this.calcWeekTotal(time)

      var currentDay = firstDay
      for (var i = 0; i < selectedDays.length; i++) {
        if (selectedDays[i] === this.selectedValue) {
          this[this.dayMap[currentDay]].selected += 1

          var date = time.dayOfYear(i + 1).date()
          this.dateState[date].selected += 1
        }
        currentDay = (currentDay + 1) % 7
      }

      this.updateWeekBtnState([0, 1, 2, 3, 4, 5, 6])
      var datesToBeUpdated = []
      for (var j = 1; j <= 31; j++) {
        datesToBeUpdated.push(j)
      }
      this.updateDateBtnState(datesToBeUpdated)
    },
    onMonthChange ({year, month}) {
      this.month = month
      var time = moment().year(year).month(month).date(1)
      var daysInMonth = time.daysInMonth()
      var firstDayIndex = time.dayOfYear() - 1
      var selectedDays = this.selected[year] || []

      this.resetMonthState()
      this.monthState.total = daysInMonth
      this.monthState.startIndex = firstDayIndex
      this.monthState.endIndex = firstDayIndex + daysInMonth

      var lastDayIndex = selectedDays.length >= (firstDayIndex + daysInMonth) ? firstDayIndex + daysInMonth : selectedDays.length
      for (var i = firstDayIndex; i < lastDayIndex; i++) {
        if (selectedDays[i] === this.selectedValue) {
          this.monthState.selected += 1
        }
      }

      this.updateMonthBtnState()
    },
    clickWeekQuickSelect (day) {
      day = day - 0
      var state = this.weekBtnState[this.dayMap[day]] === 2 ? this.unSelectedValue : this.selectedValue
      this.changeWeekDayState(this.year, [day], state)
    },
    clickMonthQuickSelect () {
      var state = this.monthBtnState === 2 ? this.unSelectedValue : this.selectedValue
      this.changeMonthState(this.year, this.month, state)
    },
    clickInverseMonthQuickSelect () {
      this.changeMonthState(this.year, this.month)
    },
    clickDateQuickSelect (date) {
      var state = this.dateBtnState[date] === 2 ? this.unSelectedValue : this.selectedValue
      this.changeDateState(this.year, [date], state)
    },
    updateAllState (dayIndex, state, year, date, day, changedDates, changedWeekDays) {
      if (this.selected[year][dayIndex] !== state) {
        this.updateDateState(date, state)
        if (changedDates && changedDates.indexOf(date) < 0) {
          changedDates.push(date)
        }

        this.updateWeekState(day, state)
        if (changedWeekDays && changedWeekDays.indexOf(day) < 0) {
          changedWeekDays.push(day)
        }

        this.updateMonthState(dayIndex, state)
      }
    },
    changeWeekDayState (year, days, state) {
      if (!this.selected[year]) {
        this.$set(this.selected, year, [])
      }
      var time = moment().year(year).dayOfYear(1)
      var firstDay = time.day()

      var changedDates = []
      var changedWeekDays = []
      var selectedDays = [].concat(this.selected[year])
      for (var i = 0; i < days.length; i++) {
        var day = days[i]
        var dayIndex = (day - firstDay) < 0 ? 7 + day - firstDay : day - firstDay

        var weeks = this[this.dayMap[day]].total
        for (var w = 0; w < weeks; w++) {
          selectedDays[dayIndex] = state
          var date = time.dayOfYear(dayIndex + 1).date()
          this.updateAllState(dayIndex, state, year, date, day, changedDates, changedWeekDays)
          dayIndex += 7
        }
      }

      this.updateWeekBtnState(changedWeekDays)
      this.updateDateBtnState(changedDates)
      this.updateMonthBtnState()
      this.selected[year] = selectedDays
    },
    changeDateState (year, dates, state) {
      if (!this.selected[year]) {
        this.$set(this.selected, year, [])
      }
      var selectedDays = [].concat(this.selected[year])

      var time = moment().year(year)
      var changedWeekDays = []
      var changedDates = []
      for (var d = 0; d < dates.length; d++) {
        var date = dates[d]
        var firstDayIndex = date - 1
        var totalDaysIndex = 0
        for (var i = 0; i < this.datesInMonth.length; i++) {
          var day = time.month(i).date(date).day()
          totalDaysIndex += this.datesInMonth[i]
          if (firstDayIndex < totalDaysIndex) {
            selectedDays[firstDayIndex] = state
            this.updateAllState(firstDayIndex, state, year, date, day, changedDates, changedWeekDays)
            firstDayIndex += this.datesInMonth[i]
          } else {
            firstDayIndex = totalDaysIndex - 1 + date
          }
        }
      }

      this.updateDateBtnState(changedDates)
      this.updateWeekBtnState(changedWeekDays)
      this.updateMonthBtnState()
      this.selected[year] = selectedDays
    },
    changeMonthState (year, month, state) {
      // state undefined means to inverse

      if (!this.selected[year]) {
        this.$set(this.selected, year, [])
      }
      var selectedDays = [].concat(this.selected[year])

      var time = moment().year(year).month(month).date(1)
      var dayIndex = time.dayOfYear() - 1
      var daysInMonth = time.daysInMonth()
      var currentDay = time.day()

      var changedDates = []
      var changedWeekDays = []
      var date = 1
      for (var i = dayIndex; i < dayIndex + daysInMonth; i++) {
        if (state === undefined) {
          if (selectedDays[i] !== this.selectedValue) {
            selectedDays[i] = this.selectedValue
          } else {
            selectedDays[i] = this.unSelectedValue
          }
        } else {
          selectedDays[i] = state
        }

        this.updateAllState(i, selectedDays[i], year, date, currentDay, changedDates, changedWeekDays)
        currentDay = (currentDay + 1) % 7
        date += 1
      }

      this.updateWeekBtnState(changedWeekDays)
      this.updateDateBtnState(changedDates)
      this.updateMonthBtnState()
      this.selected[year] = selectedDays
    },
    updateWeekState (day, state) {
      this[this.dayMap[day]].selected += state === this.selectedValue ? 1 : -1
    },
    updateWeekBtnState (days) {
      for (var i = 0; i < days.length; i++) {
        var day = this.dayMap[days[i]]
        var state = this[day].total === this[day].selected ? 2 : 1
        if (this[day].selected === 0) {
          state = 0
        }
        this.weekBtnState[day] = state
      }
    },
    resetWeekState () {
      for (var i in this.dayMap) {
        this[this.dayMap[i]].total = 0
        this[this.dayMap[i]].selected = 0
      }
    },
    calcWeekTotal (t) {
      var firstDay = t.dayOfYear(1).day()
      var lastDay = t.month(11).date(31).day()
      var daysInYear = t.dayOfYear()
      var weekInYear = ~~((daysInYear - (7 - firstDay) % 7) / 7)

      if ((7 - firstDay) % 7 !== 0) {
        for (var p = firstDay; p < 7; p++) {
          this[this.dayMap[p]].total += 1
        }
      }
      if (lastDay !== 6) {
        for (var n = 0; n <= lastDay; n++) {
          this[this.dayMap[n]].total += 1
        }
      }

      for (var i in this.dayMap) {
        this[this.dayMap[i]].total += weekInYear
      }

      return firstDay
    },
    updateMonthState (dayIndex, state) {
      if (this.monthState.startIndex <= dayIndex && dayIndex < this.monthState.endIndex) {
        this.monthState.selected += state === this.selectedValue ? 1 : -1
      }
    },
    resetMonthState () {
      this.monthState.total = 0
      this.monthState.selected = 0
      this.monthState.startIndex = 0
      this.monthState.endIndex = 0
    },
    updateMonthBtnState () {
      var state = this.monthState.selected === this.monthState.total ? 2 : 1
      if (this.monthState.selected === 0) {
        state = 0
      }
      this.monthBtnState = state
    },
    initDateState (year) {
      var time = moment().year(year)
      for (var i = 1; i <= 28; i++) {
        this.$set(this.dateState, i, { total: 12, selected: 0 })
        this.$set(this.dateBtnState, i, 0)
      }
      for (var j = 29; j <= 31; j++) {
        this.$set(this.dateBtnState, j, 0)
      }
      this.$set(this.dateState, 29, { total: 11 + (time.isLeapYear() ? 1 : 0), selected: 0 })
      this.$set(this.dateState, 30, { total: 11, selected: 0 })
      this.$set(this.dateState, 31, { total: 7, selected: 0 })
      this.datesInMonth[1] = time.isLeapYear() ? 29 : 28
    },
    updateDateState (date, state) {
      this.dateState[date].selected += state === this.selectedValue ? 1 : -1
    },
    updateDateBtnState (dates) {
      for (var i = 0; i < dates.length; i++) {
        var date = dates[i]
        this.dateBtnState[date] = this.dateState[date].selected === this.dateState[date].total ? 2 : 1
        if (this.dateState[date].selected === 0) {
          this.dateBtnState[date] = 0
        }
      }
    },
    selectChange (eventData) {
      this.updateWeekState(eventData.dayOfWeek, eventData.state)
      this.updateWeekBtnState([eventData.dayOfWeek])

      this.updateMonthState(eventData.dayOfYear - 1, eventData.state)
      this.updateMonthBtnState()

      this.updateDateState(eventData.date, eventData.state)
      this.updateDateBtnState([eventData.date])
    }
  },
  watch: {
  }
}
</script>

<style src="./calendarPicker.less" lang="less"></style>



