<template>
  <div class="vue-page-mask-container" :style="containerStyle">
    <div class="vue-page-mask" :class="{ 'show': showMask }" :style="maskStyle"></div>
    <div class="vue-page-mask-loading-container" :class="{ 'show': showLoading && showMask, 'loading': loadingState }">
      <span class="vue-page-mask-loading">
        <i class="icon-spinner mask-loading-animate"></i>
      </span>
      <span class="vue-page-mask-loading-msg" v-show="loadingMsg">{{ loadingMsg }}</span>
    </div>
  </div>
</template>

<script>
export default {
  name: 'vuePageMask',
  data () {
    return {
      maskList: [],
      showMask: false,
      color: '#000000',
      opacity: 0.2,
      showLoading: true,
      loadingState: true,
      loadingMsg: '',
      top: '0px',
      left: '0px',
      right: '0px',
      bottom: '0px',
      defOption: {
        color: '#000000',
        opacity: 0.2,
        showLoading: true,
        loadingMsg: '',
        top: '0px',
        left: '0px',
        right: '0px',
        bottom: '0px'
      }
    }
  },
  computed: {
    maskStyle () {
      return {
        top: this.top,
        left: this.left,
        right: this.right,
        bottom: this.bottom,
        'background-color': this.color,
        opacity: this.opacity
      }
    },
    containerStyle () {
      return {
        top: this.top,
        left: this.left,
        right: this.right,
        bottom: this.bottom
      }
    }
  },
  methods: {
    show (opt) {
      let newMask = this.getMaskOption(opt)
      this.maskList.push(newMask)
      if (this.maskList.length === 1) {
        this.setNewOption()
        this.showMask = true
      }
      return () => {
        this.updateList(newMask)
      }
    },
    play () {
      this.loadingState = true
    },
    pause () {
      this.loadingState = false
    },
    hideAll () {
      this.maskList = []
      this.setNewOption()
    },
    updateList (newMask) {
      let _index = this.maskList.indexOf(newMask)
      this.maskList.splice(_index, 1)
      if (_index === 0) {
        this.setNewOption()
      }
    },
    setNewOption () {
      if (this.maskList.length > 0) {
        let newOption = this.maskList[0]
        for (let key in newOption) {
          this[key] = newOption[key]
        }
        this.play()
      } else {
        this.showMask = false
      }
    },
    getMaskOption (opt) {
      var maskItem = {}
      if (typeof opt === 'string') {
        maskItem.loadingMsg = opt
        maskItem.showLoading = true
      } else if (typeof opt === 'object') {
        for (let key in this.defOption) {
          maskItem[key] = opt[key] || this.defOption[key]
        }
      }
      return maskItem
    }
  }
}
</script>

<style src="./vuePageMask.less" lang="less"></style>



