<template>
  <div class="hello">
    <h1>{{ msg }}</h1>
    <v-button @click.native="fetchTestData(4000, 'ezTest')">发送延迟ezTest请求</v-button>
    <v-button @click.native="fetchTestData(4000, '')">发送其他延迟请求</v-button>
    <v-button @click.native="fetchTestData(0, 'ezTest')">发送正常ezTest请求</v-button>
    <v-button @click.native="fetchPostData">发送post请求</v-button>
    <v-button @click.native="showConfirm(false)">打开confirm（无背板）</v-button>
    <v-button @click.native="showConfirm(true)">打开confirm</v-button>
    <v-button @click.native="hideAllConfirm">关闭所有confirm</v-button>
    <div>
      <calendar-picker :selected="selectedDateArr"></calendar-picker>
    </div>
    <div>
      {{ date }}
    </div>
  </div>
</template>

<script>
export default {
  name: 'hello',
  data () {
    return {
      msg: 'Welcome to EzSonar playground!',
      selectedDateArr: {
        2017: [0, 0, 0, 1]
      }
    }
  },
  created () {
    // this.fetchData()
  },
  computed: {
    date () {
      var tmp = {}
      for (var year in this.selectedDateArr) {
        tmp[year] = this.selectedDateArr[year].length
      }
      return tmp
    }
  },
  methods: {
    fetchData () {
      this.$service.post(this, 'test/renderPuppeteer', {
        // path: 'https://www.baidu.com',
        path: 'http://192.168.1.41:8080/dashboard',
        width: 1280,
        height: 1200
      }).then(data => {
        console.log(data)
      })
    },
    fetchPostData () {
      this.$service.post(this, 'common/postTest', { testArgs: 'haha' }, false, 0, '').then(data => {
        console.log(data)
      }, error => {
        console.log(error.message)
      })
    },
    fetchTestData (delay, apiKey) {
      delay = delay || 0
      apiKey = apiKey || ''
      let time = new Date().toLocaleTimeString()
      this.$service.get(this, 'common/test', { delay: delay, time: +new Date() }, false, 0, apiKey).then(data => {
        console.log(time + ' - ' + data.data + ' - ' + new Date().toLocaleTimeString())
      }, error => {
        console.log(error)
      })
    },
    showConfirm (backdrop) {
      var opt = {
        backdrop: backdrop,
        title: (backdrop ? '有' : '无') + '背板的确认框，标题要长点，这样才能测出问题哦',
        msg: '需要用户确认的信息，二选一，不能取消，不能取消，不能取消，重要的事情说三遍。',
        onConfirm () {
          this.$message.success('用户确认了')
        },
        onCancel () {
          this.$message.error('用户拒绝了')
        },
        confirmText: '欣然接受',
        cancelText: '残忍拒绝'
      }
      this.$confirm.show(opt)
    },
    hideAllConfirm () {
      this.$confirm.hideAll()
    }
  },
  watch: {
    '$route': 'fetchData'
  }
}
</script>

<style src="./hello.less" lang="less"></style>



