import Hello from './components/vueNotice/VueNotice'

var domState = false
var appendComponent = function (el) {
  if (!domState) {
    document.getElementsByTagName('body')[0].appendChild(el)
    domState = true
  }
}
var showNotice = function (component, type, config) {
  config.type = type
  component.addNotice(config)
}

export default {
  install: function (Vue, opt) {
    var GlobalNoticeComponent = Vue.extend(Hello)
    var globalNotice = new GlobalNoticeComponent().$mount()

    if (opt && typeof opt.duration === 'number') {
      globalNotice.defOption.duration = opt.duration
    }
    if (opt && typeof opt.transition === 'number') {
      globalNotice.transitionDuration = opt.transition
    }
    if (opt && typeof opt.reverse === 'boolean') {
      globalNotice.reverse = opt.reverse
    }

    appendComponent(globalNotice.$el)

    Vue.prototype.$notice = {
      info: function (config) {
        showNotice(globalNotice, 'info', config)
      },
      success: function (config) {
        showNotice(globalNotice, 'success', config)
      },
      error: function (config) {
        showNotice(globalNotice, 'error', config)
      },
      warning: function (config) {
        showNotice(globalNotice, 'warning', config)
      },
      open: function (config) {
        showNotice(globalNotice, 'open', config)
      }
    }
  }
}



