<template>
  <nav class="v-pagin">
    <div class="v-pagin-pageSize-select">
      <v-select v-model="pageSize" :options="pageSizeOpts" :multiple="false" :filterable="false" placement="up"></v-select>
      <span class='v-pagin-pageSize-select-unit'>{{ $t('demo.table.pageSizeUnit') }}</span>
    </div>
    <div class="v-pagin-total">
      <span class="v-pagin-total-current">{{ currentpage + 1 }}/{{ pageTotal }}</span>
      <span class='v-pagin-total-len'>{{ dataSourceLength }}</span>
    </div>
    <div class="v-pagin-pages">
      <div>
        <v-button size="small" :icon="true" shape="circle"  type='text' @click.native="onPrevClick" :disabled='pageLeftDisable'>
          <i class="icon-angle-left"></i>
        </v-button>
      </div>
      <div v-if="!showElevator" class="v-pagin-pages-wrap">
        <span  v-text="page + 1" v-for="(page, index) in pages" :class="activeNum === index ? 'active' : ''"  @click="onPageClick(index)"></span>
      </div>
      <div class="v-pagin-pages-wrap" v-else>
        <span :class="currentpage === 0 ? 'active' : ''"  @click="onPageClick(0)">1</span>
        <span v-if="showBefore" @click="clickBefore" class="v-pagin-jump-prev"><i class='icon-more-horiz'></i><i class="icon-angle-left"></i></span>
        <span v-for="page in showPagesArr" v-text="page" :class="currentpage === page - 1? 'active' : ''"  @click="onPageClick(page - 1)"></span>
        <span v-if="showBehide" @click="clickBehide" class="v-pagin-jump-next"><i class='icon-more-horiz'></i><i class="icon-angle-right"></i></span>
        <span :class="currentpage === pageTotal - 1 ? 'active' : ''"  @click="onPageClick(pageTotal - 1)" v-if="pageTotal>1">{{pageTotal}}</span>
      </div>
      <div>
        <v-button size="small" :icon="true" shape="circle"  type='text'  @click.native="onNextClick" :disabled='pageRightDisable'>
          <i class="icon-angle-right"></i>
        </v-button>
      </div>
    </div>
  </nav>
</template>
<script>
// import vSelect from '../../../vueSelect/VueSelect'
import bus from '../assets/eventBus'
export default {
  props: {
    // 显示个数数组
    pageSizeOpts: {
      type: Array,
      default: function () {
        return [
          {
            label: '5',
            value: 5
          },
          {
            label: '10',
            value: 10
          },
          {
            label: '20',
            value: 20
          },
          {
            label: '30',
            value: 30
          }
        ]
      }
    },
    // 表格数据（数组）
    // dataSource: {
    //   type: Array,
    //   default: function () {
    //     return []
    //   }
    // },
    dataSourceLen: {
      type: Number,
      default: 1
    },
    // 显示页数
    showPages: {
      type: Number,
      default: 7
    },
    showElevator: {
      type: Boolean,
      default: true
    }
  },
  data () {
    return {
      activeNum: 0,
      pages: [0],
      pageTotal: 1,
      renderList: [],
      currentpage: 0, // 当前页 从第0页开始
      pagelist: {
        start: 0,
        end: 4
      },
      pageSize: 5,
      showPagesArr: []
    }
  },
  methods: {
        // 点击页码刷新数据
    onPageClick (index) {
      if (this.showElevator) {
        this.currentpage = index
      } else {
        this.currentpage = this.pages[index]
      }
      this.getPages()
      this.getData()
    },
        // 上一页
    onPrevClick () {
      // 当前页是否为当前最小页码
      if (this.currentpage > 0) {
        this.currentpage = this.currentpage - 1
        this.getPages()
        this.getData()
      }
    },
        // 下一页
    onNextClick () {
      // 当前页是否为当前最大页码
      if (this.currentpage < this.pageTotal - 1) {
        this.currentpage = this.currentpage + 1
        this.getPages()
        this.getData()
      }
    },
        // 获取页码
    getPages () {
      this.pages = []
      this.showPagesArr = []
      // 获得一共几页
      this.pageTotal = Math.ceil(this.dataSourceLen / this.pageSize)
      // 如果当前页不是小于页面总数 将当前页面改在最后一页
      if (this.currentpage >= this.pageTotal) {
        this.currentpage = this.pageTotal - 1
      }
        // 比较总页码和显示页数,如果显示的页数大于等于总页数，则显示全
      if (this.pageTotal <= this.showPages) {
        if (this.showElevator) {
          for (let i = 2; i < this.pageTotal; i++) {
            this.showPagesArr.push(i)
          }
        } else {
          for (let i = 0; i < this.pageTotal; i++) {
            this.pages.push(i)
          }
        }
        this.activeNum = this.currentpage
      } else {
        // 否则显示一部分
        if (this.showElevator) {
          var halfDistance = Math.floor((this.showPages - 2) / 2)
          if (this.currentpage + 1 - halfDistance > 1) {
            if (this.currentpage + 1 + this.showPages - 2 - halfDistance < this.pageTotal) {
              for (let i = this.currentpage + 1 - halfDistance; i < this.currentpage + 1 + this.showPages - 2 - halfDistance; i++) {
                this.showPagesArr.push(i)
              }
            } else {
              for (let i = this.pageTotal - (this.showPages - 2); i < this.pageTotal; i++) {
                this.showPagesArr.push(i)
              }
            }
          } else {
            if (this.currentpage + 1 + this.showPages - 2 - halfDistance < this.pageTotal) {
              for (let i = 2; i < 2 + (this.showPages - 2); i++) {
                this.showPagesArr.push(i)
              }
            } else {
              for (let i = 2; i < this.pageTotal; i++) {
                this.showPagesArr.push(i)
              }
            }
          }
        } else {
          var fromwhere = (Math.floor(this.currentpage / this.showPages)) * this.showPages
          for (let i = 0; i < this.showPages; i++) {
            if ((fromwhere + i) < this.pageTotal) {
              this.pages.push(fromwhere + i)
              if ((fromwhere + i) === this.currentpage) {
                this.activeNum = i
              }
            }
          }
        }
      }
    },
    // 页码变化获取数据
    getData () {
      let pageNum = this.currentpage + 1
      if (pageNum === 1) {
        if (this.dataSourceLen < this.pageSize) {
          this.pagelist.start = 0
          this.pagelist.end = this.dataSourceLen
        } else {
          this.pagelist.start = 0
          this.pagelist.end = this.pageSize - 1
        }
      } else if (pageNum === this.pageTotal) {
        this.pagelist.start = (pageNum - 1) * this.pageSize
        this.pagelist.end = this.dataSourceLen - 1
      } else {
        this.pagelist.start = (pageNum - 1) * this.pageSize
        this.pagelist.end = pageNum * this.pageSize - 1
      }
      this.$emit('getRenderData', this.pagelist, this.currentpage + 1, this.pageSize)
      // this.renderList = this.dataSource.slice(this.pageSize * this.currentpage, this.pageSize * this.currentpage + this.pageSize)
      // this.$emit('getRenderList', this.renderList)
      // if (this.renderList.length === 0) {
      //   this.currentpage = 0
      // }
    },
     // 刷新表格
    refresh (targetPage) {
      if (targetPage !== undefined) {
        this.currentpage = targetPage
      }
      this.getPages()
      this.getData()
    },
    clickBefore () {
      if (this.currentpage > 0) {
        this.currentpage = this.currentpage - (this.showPages - 2) < 0 ? 0 : this.currentpage - (this.showPages - 2)
        this.getPages()
        this.getData()
      }
    },
    clickBehide () {
      if (this.currentpage < this.pageTotal - 1) {
        this.currentpage = this.currentpage + this.showPages - 2
        this.getPages()
        this.getData()
      }
    }
  },
  computed: {
    dataSourceLength () {
      return '共' + this.dataSourceLen + '条记录'
    },
    showBefore () {
      return this.showPagesArr[0] && this.showPagesArr[0] > 2
    },
    showBehide () {
      return this.showPagesArr[this.showPagesArr.length - 1] && this.showPagesArr[this.showPagesArr.length - 1] !== this.pageTotal - 1
    },
    // lenArray: {
    //   set (val) {
    //     this.len = val[0]
    //   },
    //   get () {
    //     return [this.len]
    //   }
    // },
    // initLens () {
    //   let length = this.lens.length
    //   var arr = []
    //   for (let i = 0; i < length; i++) {
    //     arr.push({label: this.lens[i], value: this.lens[i]})
    //   }
    //   return arr
    // },
    pageLeftDisable: function () {
      if (this.currentpage === 0) {
        return true
      } else {
        return false
      }
    },
    pageRightDisable: function () {
      if (this.currentpage === this.pageTotal - 1) {
        return true
      } else {
        return false
      }
    }
  },
  mounted () {
    bus.$on('refresh', (targetPage) => {
      this.refresh(targetPage)
    })
    if (this.dataSourceLen) {
      this.getPages()
      this.getData()
    }
  },
  watch: {
     // 监听显示数量
    pageSize (newVal, oldVal) {
      this.getPages()
      // let _this = this
      if (this.activeNum + 1 > this.pages.length) {
        this.activeNum = this.pages.length - 1
      }
      this.getData()
    },
    // 监测当前页变化
    activeNum (newVal, oldVal) {
      this.getData()
    },
    dataSourceLen (newVal, oldVal) {
      if (this.dataSourceLen) {
        this.getPages()
        this.getData()
      }
    }
  },
  components: {
  },
  events: {
  }
}
</script>
<style src="./pagination.less" lang="less"></style>



