// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import EzService from './service/ezService'
import VueI18n from 'vue-i18n'

import i18nMessage from './lib/i18n'
import VueMessages from './plugins/vueMessage/vueMessage'
import VueNotice from './plugins/vueNotice/vueNotice'
import VuePageMask from './plugins/vuePageMask/VuePageMask'
import VueConfirm from './plugins/vueConfirm/vueConfirm'

import App from './App'
import router from './router'

import VueMarkdown from 'vue-markdown'
import VCode from './components/vCode/VCode'
import LoadingIcon from './components/loadingIcon/LoadingIcon'

import VButton from './components/vButton/VButton'
import VButtonGroup from './components/vButton/VButtonGroup'
import VRadio from './components/vRadio/VRadio'
import VRadioGroup from './components/vRadio/VRadioGroup'
import VTabel from './components/vTabel/VTabel'
import VSelect from './components/vSelect/VSelect'
import VInput from './components/vInput/VInput'
import VInputNumber from './components/vInputNumber/VInputNumber'
import VCheck from './components/vCheck/VCheck'
import VSwitch from './components/vSwitch/VSwitch'

import Transfer from './components/transfer/Transfer'
import ScrollMask from './components/scrollMask/ScrollMask'
import MaskWrap from './components/maskWrap/MaskWrap'
import Drop from './components/drop/Drop'
import Placeholder from './components/placeholder/placeholder'
import SvgIcon from './components/svgIcon/SvgIcon'
import calendar from './components/calendar/calendar'
import calendarPicker from './components/calendar/calendarPicker'

Vue.config.productionTip = false

Vue.use(EzService, { timeout: 10000 })
Vue.use(VueI18n)

var commonComs = {
  'v-markdown': VueMarkdown,
  'v-code': VCode,
  'v-button': VButton,
  'v-button-group': VButtonGroup,
  'v-radio': VRadio,
  'v-radio-group': VRadioGroup,
  'transfer': Transfer,
  'scroll-mask': ScrollMask,
  'mask-wrap': MaskWrap,
  'v-tabel': VTabel,
  'v-select': VSelect,
  'v-input': VInput,
  'v-check': VCheck,
  'v-switch': VSwitch,
  'drop': Drop,
  'v-input-number': VInputNumber,
  'placeholder': Placeholder,
  'loading-icon': LoadingIcon,
  'svg-icon': SvgIcon,
  'calendar': calendar,
  'calendar-picker': calendarPicker
}

for (var key in commonComs) {
  Vue.component(key, commonComs[key])
}

Vue.mixin({
  methods: {
    logTheError (error) {
      if (console && console.error) {
        console.error(error)
      }
    },
    handleError (error) {
      if (!error.ok) {
        // window.location.reload()
        console.log(error)
      } else {
        if (console && console.error) {
          console.error(error)
        }
      }
    }
  }
})

const i18n = new VueI18n({
  locale: 'zh-CN',
  silentTranslationWarn: true,
  missing: (locale, key, vm) => {
    return key
  },
  messages: i18nMessage
})

const pageTitleDom = (document.head && document.head.getElementsByTagName('title')[0]) ? (document.head && document.head.getElementsByTagName('title')[0]) : null
const setHtmlPageTitle = function (newTitle) {
  if (pageTitleDom) {
    let _newTitle = newTitle ? i18n.t(newTitle) + ' | ' + i18n.t('text.bolgName') : i18n.t('text.bolgName')
    pageTitleDom.innerText = _newTitle
  }
}

router.afterEach(route => {
  let _title = route.meta.pageTitle ? route.meta.pageTitle : ''
  setHtmlPageTitle(_title)
})

Vue.use(VueMessages, { duration: 2000 })
Vue.use(VueNotice, { reverse: false })
Vue.use(VuePageMask)
Vue.use(VueConfirm, { confirmText: i18n.t('text.confirm'), cancelText: i18n.t('text.cancel') })

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  i18n,
  template: '<App/>',
  components: { App }
})



