import button from './button/Button'
import radio from './radio/Radio'
import checkbox from './checkbox/Checkbox'
import switchDemo from './switch/Switch'
import transfer from './transfer/Transfer'
import drop from './drop/drop'
import table from './table/Table'
import select from './select/Select'
import input from './input/Input'
import inputNumber from './inputNumber/InputNumber'
import pageMask from './pageMask/PageMask'
import placeholder from './placeholder/Placeholder'
import confirm from './confirm/Confirm'

var demoComponentsMixin = {
  components: {
    'icon-demo': () => import('./icon/Icon'),
    'table-demo': table,
    'select-demo': select,
    'button-demo': button,
    'radio-demo': radio,
    'input-demo': input,
    'switch-demo': switchDemo,
    'checkbox-demo': checkbox,
    'drop-demo': drop,
    'transfer-demo': transfer,
    'input-number-demo': inputNumber,
    'page-mask-demo': pageMask,
    'placeholder-demo': placeholder,
    'confirm-demo': confirm
  }
}

export default demoComponentsMixin



