<template>
  <div :class="wrapClasses">
    <template v-if="type !== 'textarea'">
      <div class="v-input-group-prepend" v-if="prepend" v-show="slotReady">
        <slot name="prepend"></slot>
      </div>
      <i class="v-input-icon v-input-prefixIcon" :class="prefixIcon" v-if="prefixIcon" @click="handlePrefixIconClick"></i>
      <i class="v-input-icon v-input-suffixIcon" :class="suffixIcon" v-if="suffixIcon" @click="handleSuffixIconClick"></i>
      <input
            :autocomplete="autocomplete"
            ref="input"
            :type="type"
            :class="inputClasses"
            :placeholder="placeholder"
            :disabled="disabled"
            :maxlength="maxlength"
            :readonly="readonly"
            :name="name"
            :value="currentValue"
            :number="number"
            @keyup.enter="handleEnter"
            @keyup="handleKeyup"
            @keydown="handleKeydown"
            @focus="handleFocus"
            @blur="handleBlur"
            @input="handleInput"
            @change="handleChange">
      <i class="v-input-icon icon-remove v-input-remove" v-show="currentValue !== ''" @click="resetValue"></i>
      <div class="v-input-group-append" v-if="append" v-show="slotReady">
        <slot name="append"></slot>
      </div>
    </template>
    <textarea
        :autocomplete="autocomplete"
        v-else
        ref="textarea"
        :class="textareaClasses"
        :style="textareaStyles"
        :placeholder="placeholder"
        :disabled="disabled"
        :rows="rows"
        :maxlength="maxlength"
        :readonly="readonly"
        :name="name"
        :value="currentValue"
        :number="number"
        @eventLister="eventLister">
    </textarea>
</div>
</template>
<script>
import { oneOf } from '../../lib/utils'
import calcTextareaHeight from './assets/calcTextareaHeight'
export default {
  props: {
    type: {
      validator (value) {
        return oneOf(value, ['text', 'textarea', 'password'])
      },
      default: 'text'
    },
    value: {
      type: [String, Number]
    },
    size: {
      validator (value) {
        return oneOf(value, ['small', 'large', 'default'])
      }
    },
    placeholder: {
      type: String,
      default: '请输入'
    },
    maxlength: {
      type: Number
    },
    disabled: {
      type: Boolean,
      default: false
    },
    prefixIcon: String,
    suffixIcon: String,
    autosize: {
      type: [Boolean, Object],
      default: false
    },
    rows: {
      type: Number,
      default: 2
    },
    readonly: {
      type: Boolean,
      default: false
    },
    autocomplete: {
      validator (value) {
        return oneOf(value, ['on', 'off'])
      },
      default: 'on'
    },
    number: {
      type: Boolean,
      default: true
    },
    name: {
      type: String
    }
  },
  data () {
    return {
      currentValue: this.value,
      prepend: true,
      append: true,
      slotReady: false,
      error: false,
      textareaStyles: {},
      eventLister: [
        {
          'keyup.enter': 'handleEnter',
          'keyup': 'handleKeyup',
          'keydown': 'handleKeydown',
          'focus': 'handleFocus',
          'blur': 'handleBlur',
          'input': 'handleInput'
        }
      ]
    }
  },
  mounted () {
    if (this.type !== 'textarea') {
      this.prepend = this.$slots.prepend !== undefined
      this.append = this.$slots.append !== undefined
    } else {
      this.prepend = false
      this.append = false
    }
    this.slotReady = true
    this.resizeTextarea()
  },
  computed: {
    wrapClasses () {
      return [
        'v-input-wrapper',
        {
          [`v-input-wrapper-${this.size}`]: !!this.size,
          'v-input-type': this.type,
          'v-input-group': this.prepend || this.append,
          [`v-input-group-${this.size}`]: (this.prepend || this.append) && !!this.size,
          'v-input-group-with-prepend': this.prepend,
          'v-input-group-with-append': this.append,
          'v-input-hide-icon': this.append,
          'v-input-error': this.error,
          'v-input-readonly': this.readonly
        }
      ]
    },
    inputClasses () {
      return [
        'v-input',
        {
          [`v-input-${this.size}`]: !!this.size,
          'v-input-disabled': this.disabled
        }
      ]
    },
    textareaClasses () {
      return [
        'v-input',
        {
          'v-input-disabled': this.disabled
        }
      ]
    }
  },
  methods: {
    resetValue () {
      this.currentValue = ''
    },
    handleEnter (event) {
      this.$emit('on-enter', event)
    },
    handleKeydown (event) {
      this.$emit('on-keydown', event)
    },
    handleKeyup (event) {
      let value = event.target.value
      if (value.toString().length >= this.maxlength) {
        this.error = true
      } else {
        this.error = false
      }
      this.$emit('on-keyup', event)
    },
    handlePrefixIconClick (event) {
      this.$emit('on-prefix-click', event)
    },
    handleSuffixIconClick (event) {
      this.$emit('on-suffix-click', event)
    },
    handleFocus (event) {
      this.$emit('on-focus', event)
    },
    handleBlur (event) {
      this.$emit('on-blur', event)
    },
    handleInput (event) {
      let value = event.target.value
      if (this.number) {
        if (value.toString().length) {
          value = Number.isNaN(Number(value)) ? value : Number(value)
        } else {
          value = ''
        }
      }
      this.$emit('input', value)
      this.setCurrentValue(value)
      this.$emit('on-change', event)
    },
    handleChange (event) {
      this.$emit('on-input-change', event)
    },
    setCurrentValue (value) {
      if (value === this.currentValue) return
      this.$nextTick(() => {
        this.resizeTextarea()
      })
      this.currentValue = value
    },
    resizeTextarea () {
      const autosize = this.autosize
      if (!autosize || this.type !== 'textarea') {
        return false
      }
      const minRows = autosize.minRows
      const maxRows = autosize.maxRows
      this.textareaStyles = calcTextareaHeight(this.$refs.textarea, minRows, maxRows)
    },
    focus () {
      if (this.type === 'textarea') {
        this.$refs.textarea.focus()
      } else {
        this.$refs.input.focus()
      }
    },
    blur () {
      if (this.type === 'textarea') {
        this.$refs.textarea.blur()
      } else {
        this.$refs.input.blur()
      }
    }
  },
  watch: {
    value (val, oldVal) {
      this.setCurrentValue(val)
    }
  }
}
</script>

<style src="./vInput.less" lang="less"></style>



