<template>
  <div class="loader">
    <div class="loader-inner ball-beat">
      <div v-for='n in len'></div>
    </div>
  </div>
</template>
<script>
export default {
  props: {
    len: {
      type: Number,
      default: 3
    }
  }
}
</script>

<style src="./loadingIcon.less" lang="less"></style>



