<template>
  <a class="v-button" :class="btnClasses" :disabled="disabled">
    <span v-show="loading" class="v-button-loading"><i class="v-button-loading-icon icon-spinner2"></i></span><slot></slot>
  </a>
</template>

<script>
export default {
  props: {
    type: {
      required: false,
      type: String,
      default: 'default'
    },
    size: {
      required: false,
      type: String,
      default: 'default'
    },
    shape: {
      required: false,
      type: String,
      default: 'default'
    },
    icon: {
      required: false,
      type: Boolean,
      default: false
    },
    loading: {
      required: false,
      type: Boolean,
      default: false
    },
    disabled: {
      required: false,
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      sizeMap: {
        large: 'large',
        default: 'default',
        small: 'small',
        mini: 'mini'
      },
      typeMap: {
        default: '',
        ghost: 'ghost',
        text: 'text',
        primary: 'primary',
        info: 'info',
        success: 'success',
        error: 'error',
        warning: 'warning'
      },
      shapeMap: {
        default: '',
        circle: 'circle'
      },
      iconClassName: 'icon',
      loadingClassName: 'loading',
      attrs: [
        {
          map: 'sizeMap',
          prop: 'size',
          defaultClass: 'default'
        },
        {
          map: 'typeMap',
          prop: 'type',
          defaultClass: ''
        },
        {
          map: 'shapeMap',
          prop: 'shape',
          defaultClass: ''
        }
      ]
    }
  },
  computed: {
    propsClasses () {
      let classes = []
      for (let attr of this.attrs) {
        let className = this[attr.map][this[attr.prop]] || attr.defaultClass
        if (className) {
          classes.push(className)
        }
      }
      return this.icon ? classes.concat([this.iconClassName]) : classes
    },
    btnClasses () {
      return this.loading ? this.propsClasses.concat([this.loadingClassName]) : this.propsClasses
    }
  }
}
</script>
<style src="./vButton.less" lang="less"></style>



