<template>
  <div class="api-body" ref="apiDom">
  </div>
</template>

<script>
import { SwaggerUIBundle, SwaggerUIStandalonePreset } from 'swagger-ui-dist'
import 'swagger-ui-dist/swagger-ui.css'

export default {
  name: 'api',
  props: {
    contentUrl: {
      required: false,
      type: String,
      default: ''
    },
    contentTimestamp: {
      required: false,
      type: String,
      default: ''
    }
  },
  data () {
    return {
      apiJson: null,
      apiUI: null
    }
  },
  mounted () {
    this.fetchData()
  },
  computed: {
  },
  methods: {
    fetchData () {
      if (this.contentUrl) {
        this.$service.cache.static(this, this.contentUrl, {}, false, this.contentTimestamp).then(data => {
          this.apiJson = data
          this.renderApiUI()
        })
      }
      this.apiJson = null
    },
    renderApiUI () {
      if (this.apiJson) {
        this.apiUI = SwaggerUIBundle({
          spec: this.apiJson,
          domNode: this.$refs.apiDom,
          presets: [
            SwaggerUIBundle.presets.apis,
            SwaggerUIStandalonePreset
          ],
          plugins: [
            SwaggerUIBundle.plugins.DownloadUrl
          ],
          layout: 'StandaloneLayout'
        })
      }
    }
  },
  watch: {
    contentUrl () {
      this.fetchData()
    }
  }
}
</script>

<style src="./api.less" lang="less"></style>



