<template>
  <i class="svg-icon" :class="classes">
    <svg width="100%" height="100%" viewBox="0 0 32 32">
      <use :xlink:href="link"></use>
    </svg>
  </i>
</template>

<script>
export default {
  props: {
    iconName: {
      required: true,
      type: String
    },
    themeSeparator: {
      required: false,
      type: String,
      default: '.'
    }
  },
  data () {
    return {
    }
  },
  computed: {
    icon () {
      return this.iconName.split(this.themeSeparator)[0]
    },
    classes () {
      return this.iconName.split(this.themeSeparator)
    },
    link () {
      return this.$service.staticUrl + '/assets/svgset.svg#' + this.icon
    }
  }
}
</script>

<style src="./svgIcon.less" lang="less"></style>



