<template>
  <div class="vue-confirm-outer-container">
    <div class="vue-confirm-backdrop" :class="{ 'show': backdrop && showConfirm }" :style="backdropStyle"></div>
    <div class="vue-confirm-container" :class="{ 'show': showConfirm }">
      <div class="vue-confirm-title" v-show="title">
        <mask-wrap direction="ltr" :title="title">{{ title }}</mask-wrap>
      </div>
      <div class="vue-confirm-msg" v-show="msg">{{ msg }}</div>
      <div class="vue-confirm-actions">
        <v-button type="text" @click.native="cancelCB">{{ cancelText }}</v-button>
        <v-button type="primary" @click.native="confirmCB">{{ confirmText }}</v-button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'vueConfirm',
  data () {
    return {
      confirmList: [],
      showConfirm: false,
      color: '#000000',
      opacity: 0.2,
      backdrop: true,
      title: '',
      msg: '',
      confirmText: 'OK',
      cancelText: 'Cancel',
      onConfirm () {},
      onCancel () {},
      defOption: {
        color: '#000000',
        opacity: 0.2,
        backdrop: true,
        title: '',
        msg: '',
        confirmText: 'OK',
        cancelText: 'Cancel',
        onConfirm () {},
        onCancel () {}
      }
    }
  },
  computed: {
    backdropStyle () {
      return {
        'background-color': this.color,
        opacity: this.opacity
      }
    }
  },
  methods: {
    show (opt) {
      let newConfirm = this.getConfirmOption(opt)
      this.confirmList.push(newConfirm)
      if (this.confirmList.length === 1) {
        this.setNewOption()
        this.showConfirm = true
      }
      return () => {
        this.updateList(newConfirm)
      }
    },
    confirmCB () {
      this.onConfirm()
      this.close()
    },
    cancelCB () {
      this.onCancel()
      this.close()
    },
    hideAll () {
      this.confirmList = []
      this.setNewOption()
    },
    close () {
      this.confirmList.splice(0, 1)
      this.setNewOption()
    },
    updateList (newMask) {
      let _index = this.confirmList.indexOf(newMask)
      this.confirmList.splice(_index, 1)
      if (_index === 0) {
        this.setNewOption()
      }
    },
    setNewOption () {
      if (this.confirmList.length > 0) {
        let newOption = this.confirmList[0]
        for (let key in newOption) {
          this[key] = newOption[key]
        }
      } else {
        this.showConfirm = false
      }
    },
    getConfirmOption (opt) {
      var maskItem = {}
      for (let key in this.defOption) {
        if (key === 'msg') {
          maskItem[key] = opt[key] || opt || this.defOption[key]
        } else if (key === 'backdrop') {
          maskItem[key] = typeof opt[key] === 'undefined' ? this.defOption[key] : opt[key]
        } else {
          maskItem[key] = opt[key] || this.defOption[key]
        }
      }
      return maskItem
    }
  }
}
</script>

<style src="./vueConfirm.less" lang="less"></style>



