<template>
  <div class="mask-wrap-container" :class="classes">
    <slot></slot>
  </div>
</template>

<script>
export default {
  props: {
    direction: {
      required: false,
      type: String,
      default: 'v'
    }
  },
  data () {
    return {
      classMap: {
        'ltr': 'mask-ltr',
        'rtl': 'mask-rtl',
        'v': 'mask-v',
        'h': 'mask-h'
      }
    }
  },
  computed: {
    classes () {
      return this.classMap[this.direction] || ''
    }
  }
}
</script>
<style src="./maskWrap.less" lang="less"></style>



