import Vue from 'vue'
import Router from 'vue-router'

import config from '../build-config'

import * as routerNames from './routerNames'

import Hello from '@/views/hello/Hello'
import NotFound from '@/views/notFound/NotFound'
import Header from '@/views/header/Header'
import Content from '@/views/content/Content'

import Post from '@/components/post/Post'
import Api from '@/components/api/Api'

import componentList from '../views/content/assets/componentList'
import apiList from '../views/content/assets/apiList'

Vue.use(Router)

var historyMode = false

function getFixedUrl (url) {
  return url.slice(0, url.lastIndexOf('/'))
}

function getRouter (routes, rootUrl) {
  for (var route of routes) {
    if (historyMode) {
      if (route.path.indexOf('/') === 0) {
        route.path = getFixedUrl(rootUrl) + route.path
      }
    }
  }
  return routes
}

var routes = [
  {
    path: '/',
    redirect: {
      name: routerNames.HELLO
    }
  },
  {
    path: '/home',
    name: routerNames.HELLO,
    components: {
      header: Header,
      body: Hello
    },
    meta: {
      pageTitle: `router.${routerNames.HELLO}`
    }
  },
  {
    path: '/component',
    name: routerNames.COMPONENT,
    components: {
      header: Header,
      body: Content
    },
    meta: {
      detail: routerNames.COMPONENTDETAIL,
      pageTitle: `router.${routerNames.COMPONENT}`,
      contentData: componentList
    },
    children: [
      {
        path: ':subName',
        name: routerNames.COMPONENTDETAIL,
        component: Post,
        meta: {
          detail: routerNames.COMPONENTDETAIL,
          pageTitle: `router.${routerNames.COMPONENTDETAIL}`,
          contentData: componentList
        }
      }
    ]
  },
  {
    path: '/apidoc',
    name: routerNames.API,
    components: {
      header: Header,
      body: Content
    },
    meta: {
      detail: routerNames.APIDETAIL,
      pageTitle: `router.${routerNames.API}`,
      contentData: apiList
    },
    children: [
      {
        path: ':subName',
        name: routerNames.APIDETAIL,
        component: Api,
        meta: {
          detail: routerNames.APIDETAIL,
          pageTitle: `router.${routerNames.APIDETAIL}`,
          contentData: apiList
        }
      }
    ]
  },
  {
    path: '/404',
    name: routerNames.NOTFOUND,
    alias: '*',
    components: {
      header: Header,
      body: NotFound
    },
    meta: {
      pageTitle: `router.${routerNames.NOTFOUND}`
    }
  }
]

export default new Router({
  mode: historyMode ? 'history' : 'hash',
  routes: getRouter(routes, config.rootUrl)
})



