<template>
  <div :class="classes">
    <div ref="toggle" 
         @click.prevent="toggleDropdown" 
         class="v-select-selection" >
      <div class="v-select-tag" v-for="(item, index) in selectedMultiple">
        <span class="v-select-tag-text">{{ item.label }}</span>
        <v-button size="small" :icon="true" @click.native.stop="removeTag(index)" class="v-select-tag-close">
          <i class="icon-remove"></i>
        </v-button>
      </div>
      <span class="v-select-placeholder" v-show="showPlaceholder && !filterable">{{ localePlaceholder }}</span>
      <span v-show="!showPlaceholder && !multiple && !filterable" class="v-select-selected-value">{{ selectedSingle }}</span>
      <input
        type="text"
        ref="input"
        v-if="filterable"
        v-model="query"
        :disabled="disabled"
        :style="inputStyle"
        @blur="handleBlur"
        @keydown="resetInputState"
        @keydown.delete="handleInputDelete"
        @keyup.stop="searchQuery"
        class="v-select-input"
        :placeholder="showPlaceholder ? localePlaceholder : ''"
      />
      <v-button size="small" :icon="true" class="v-select-arrow">
        <i class="icon-caret-down"></i>
      </v-button>
    </div>
    <transition :name="transitionName" :duration="4000">
      <drop class="v-select-dropDown" :show.sync="dropVisible" :direction="placement" ref="dropdown">
        <div class="v-select-not-found" v-if="!filteredOptions.length">
          <div>无匹配数据</div>
        </div>
        <div class="v-select-dropdown-list" v-else>
          <scroll-mask max-length="10em">
            <div class="v-select-item" 
                v-for="(item, index) in filteredOptions"
                @click.prevent.stop="selectOpt(item)" 
                :class="{ 'v-select-item-selected': isOptionSelected(item), 'v-select-item-hover' : isOptionMouseover(index, item), 'v-select-item-disabled': isOptionDisabled(item) }"
                @mouseover="chooseHover(index)">{{ item.label }}</div>
          </scroll-mask>
        </div>
      </drop>
      <!--<div ref="dropdown" class="v-select-dropDown" v-show="dropVisible" :class="placement === 'top' ? 'select-dropDown-top' : ''">
        <ul class="v-select-not-found" v-show="!filteredOptions.length">
          <li>无匹配数据</li>
        </ul>
        <ul class="v-select-dropdown-list" v-show="filteredOptions.length">
          <li class="v-select-item" 
              v-for="(item, index) in filteredOptions"
              @click.prevent.stop="selectOpt(item)" 
              :class="{ 'v-select-item-selected': isOptionSelected(item) }">{{ item.label }}</li>
        </ul>
      </div>-->
    </transition>
  </div>
</template>

<script>
import { oneOf } from '../../lib/utils'
export default {
  data () {
    return {
      query: '',
      visible: false,
      focusVisible: false,
      inputLength: 20,
      model: this.value,
      selectedSingle: '',    // label
      selectedMultiple: [],
      notFoundShow: false,
      filteredOptions: this.options,
      focusIndex: 0
    }
  },
  props: {
    value: {
      type: [String, Number, Array],
      default: ''
    },
    options: {
      type: Array,
      default () {
        return []
      }
    },
    multiple: {
      type: Boolean,
      default: false
    },
    disabled: {
      type: Boolean,
      default: false
    },
    placeholder: {
      type: String,
      default: '请选择'
    },
    filterable: {
      type: Boolean,
      default: false
    },
    placement: {
      validator (value) {
        return oneOf(value, ['up', 'down'])
      },
      default: 'down'
    },
    optionDisabled: {
      type: Array,
      default () {
        return []
      }
    }
  },
  components: {
  },
  created () {
  },
  mounted () {
    this.modelToQuery()
    document.addEventListener('keydown', this.handleKeydown)
    if (this.multiple) {
      this.updateMultipleSelected()
    } else {
      this.updateSingleSelected()
    }
  },
  beforeDestroy () {
    document.removeEventListener('keydown', this.handleKeydown)
  },
  methods: {
    selectOpt (opt) {
      if (this.isOptionDisabled(opt)) {
        return false
      }
      if (this.isOptionSelected(opt)) {
        this.deselectOpt(opt)
      } else {
        if (this.multiple) {
          this.model.push(opt.value)
          this.query = ''
        } else {
          this.model = opt.value
          this.hideDropdown()
        }
      }
    },
    isOptionSelected (opt) {
      if (this.multiple) {
        return oneOf(opt.value, this.model)
      } else {
        if (this.model === opt.value) {
          return true
        } else {
          return false
        }
      }
    },
    deselectOpt (opt) {
      if (this.multiple) {
        let index = this.model.indexOf(opt.value)
        this.model.splice(index, 1)
        this.query = ''
      }
    },
    toggleDropdown () {
      if (this.disabled) {
        return false
      }
      if (this.filterable) {
        this.$refs.input.focus()
      }
      this.visible = !this.visible
    },
    hideDropdown () {
      this.visible = false
      this.focusIndex = 0
    },
    handleBlur () {},
    searchQuery () {
      if (!this.focusVisible) {
        let self = this
        this.filteredOptions = this.options.filter(function (opt) {
          return opt.label.toLowerCase().indexOf(self.query.toLowerCase()) !== -1
        })
        this.visible = true
      }
    },
    resetInputState () {
      this.inputLength = this.$refs.input.value.length * 12 + 20
    },
    handleInputDelete () {
      if (this.multiple && this.model.length && this.query === '') {
        this.removeTag(this.model.length - 1)
      }
    },
    modelToQuery () {
      if (!this.multiple && this.filterable && this.model !== undefined) {
        this.options.forEach((child) => {
          if (this.model === child.value) {
            if (child.label) {
              this.query = child.label
            } else {
              this.query = child.value
            }
          }
        })
      }
    },
    updateSingleSelected () {
      const type = typeof this.model
      if (type === 'string' || type === 'number') {
        let findModel = false
        for (let i = 0; i < this.options.length; i++) {
          if (this.model === this.options[i].value) {
            this.selectedSingle = this.options[i].label
            findModel = true
            break
          }
        }
        if (!findModel) {
          this.model = ''
          this.query = ''
        }
      }
    },
    updateMultipleSelected () {
      if (this.multiple && Array.isArray(this.model)) {
        let selected = []
        for (let i = 0; i < this.model.length; i++) {
          const model = this.model[i]
          for (let j = 0; j < this.options.length; j++) {
            const option = this.options[j]
            if (model === option.value) {
              selected.push({
                value: option.value,
                label: option.label
              })
            }
          }
        }
        this.selectedMultiple = selected
      }
    },
    removeTag (index) {
      if (this.disabled) {
        return false
      }
      this.model.splice(index, 1)
      if (this.filterable && this.visible) {
        this.$refs.input.focus()
      }
    },
    isOptionDisabled (item) {
      return this.optionDisabled.some(function (opt) {
        return opt === item.value
      })
    },
    isOptionMouseover (index, item) {
      if (this.isOptionDisabled(item)) {
        return false
      }
      return this.focusIndex === index
    },
    chooseHover (index) {
      this.focusIndex = index
    },
    handleKeydown (e) {
      if (this.visible) {
        const keyCode = e.keyCode
        // Esc
        if (keyCode === 27) {
          e.preventDefault()
          this.hideDropdown()
        }
        // next
        if (keyCode === 40) {
          e.preventDefault()
          this.navigateOptions('next')
        }
        // prev
        if (keyCode === 38) {
          e.preventDefault()
          this.navigateOptions('prev')
        }
        // enter
        if (keyCode === 13) {
          e.preventDefault()
          this.focusVisible = true
          this.selectOpt(this.filteredOptions[this.focusIndex])
        }
      }
    },
    navigateOptions (direction) {
      if (direction === 'next') {
        this.focusIndex = (this.focusIndex === this.filteredOptions.length - 1) ? 0 : this.focusIndex + 1
      } else if (direction === 'prev') {
        this.focusIndex = (this.focusIndex <= 0) ? this.filteredOptions.length - 1 : this.focusIndex - 1
      }
      this.resetScrollTop()
    },
    resetScrollTop () {
      let parentUl = this.$refs.dropdown.$el
      let target = parentUl.children[0].children[0].children[0].children[this.focusIndex]
      let bottomOverflowDistance = target.getBoundingClientRect().bottom - parentUl.getBoundingClientRect().bottom
      let topOverflowDistance = target.getBoundingClientRect().top - parentUl.getBoundingClientRect().top

      if (bottomOverflowDistance > 0) {
        parentUl.children[0].children[0].children[0].scrollTop += bottomOverflowDistance
      }
      if (topOverflowDistance < 0) {
        parentUl.children[0].children[0].children[0].scrollTop += topOverflowDistance
      }
    }
  },
  computed: {
    dropVisible: {
      get () {
        let status = true
        if (this.query === '' && !this.options.length) status = false
        if (!this.options.length) status = false
        return this.visible && status
      },
      set (val) {
        this.visible = val
      }
    },
    localePlaceholder () {
      if (this.placeholder === undefined) {
        return '请选择'
      } else {
        return this.placeholder
      }
    },
    classes () {
      return [
        'v-select',
        {
          'v-select-visible': this.visible,
          'v-select-disabled': this.disabled,
          'v-select-multiple': this.multiple,
          'v-select-single': !this.multiple
        }
      ]
    },
    showPlaceholder () {
      let status = false
      if ((typeof this.model) === 'string') {
        if (this.model === '') {
          status = true
        }
      } else if (Array.isArray(this.model)) {
        if (!this.model.length) {
          status = true
        }
      } else if (this.model === null) {
        status = true
      }
      return status
    },
    transitionName () {
      return this.placement === 'bottom' ? 'slide-up' : 'slide-down'
    },
    inputStyle () {
      let style = {}
      if (this.multiple) {
        if (this.showPlaceholder) {
          style.width = '100%'
        } else {
          style.width = `${this.inputLength}px`
        }
      }
      return style
    }
  },
  watch: {
    value (val) {
      this.model = val
      if (val === '') this.query = ''
    },
    label (val) {
      this.currentLabel = val
      this.updateLabel()
    },
    model () {
      this.$emit('input', this.model)
      this.modelToQuery()
      if (this.multiple) {
        this.updateMultipleSelected()
      } else {
        this.updateSingleSelected()
      }
    },
    query () {
      if (!this.query) {
        this.filteredOptions = this.options
      }
    }
  }
}
</script>

<style src="./vSelect.less" lang="less"></style>



