<template>
  <div class="vue-message-container" :class="{ 'show': showMessage, 'with-close': closable }">
    <i class="vue-message-type-icon" :class="classMap[type]"></i>
    <span>{{ msg }}</span>
    <a class="vue-message-close-btn" :class="{ 'show': closable }" @click="hide">
      <i class="icon-remove"></i>
    </a>
  </div>
</template>

<script>
export default {
  name: 'vueMessage',
  data () {
    return {
      msg: '',
      showMessage: false,
      type: 'info',
      transitionDuration: 200,
      transitionState: false,
      duration: 1500,
      closeTimer: null,
      classMap: {
        'info': ['icon-info-sign', 'message-info'],
        'success': ['icon-ok-sign', 'message-success'],
        'warning': ['icon-exclamation-circle', 'message-warning'],
        'error': ['icon-remove-sign', 'message-error'],
        'loading': ['icon-spinner2', 'message-loading', 'loading-animate']
      },
      onClose: null,
      defOption: {
        type: 'info',
        msg: '',
        duration: 1500,
        onClose: null
      }
    }
  },
  computed: {
    closable () {
      return this.duration === 0 && this.type !== 'loading'
    }
  },
  methods: {
    show (opt, type) {
      if (!this.closable && !this.transitionState) {
        this.setOption(opt, type)
        this.setCloseTimer()
        this.showMessage = true
      } else {
        console && console.error('上一个通知尚未处理!')
      }
    },
    hide () {
      if (typeof this.onClose === 'function') {
        this.onClose()
      }
      this.showMessage = false
      this.restOption()
    },
    setCloseTimer () {
      if (typeof this.duration === 'number') {
        this.clearCloseTimer()
        if (this.duration > 0) {
          this.closeTimer = setTimeout(this.hide, this.duration)
        }
      }
    },
    clearCloseTimer () {
      if (this.closeTimer) {
        clearTimeout(this.closeTimer)
        this.closeTimer = null
      }
    },
    restOption () {
      this.transitionState = true
      setTimeout(() => {
        this.duration = this.defOption.duration
        this.onClose = this.defOption.onClose
        this.type = this.defOption.type
        this.msg = this.defOption.msg
        this.transitionState = false
      }, this.transitionDuration)
    },
    setOption (opt, type) {
      this.type = type || this.defOption.type

      if (typeof opt === 'string') {
        this.msg = opt
      } else if (typeof opt === 'object') {
        this.msg = opt.msg || this.defOption.msg
        this.duration = typeof opt.duration === 'number' ? opt.duration : this.defOption.duration
        this.onClose = opt.onClose || this.defOption.onClose
      }
    }
  }
}
</script>

<style src="./vueMessage.less" lang="less"></style>



