<template>
  <div class="v-table">
    <div class="v-table-tr v-table-head" :style="{ paddingRight: paddingRight}">
      <div v-for="(col, index) in columns" class="v-table-cell v-table-head-cell">{{ col.title }}</div>
    </div>
    <div class="v-table-body" :style="{ height: tableBodyHeight }" ref="scroll" @mousewheel="stopEvent($event)" id="ioio">
      <slot></slot>
    </div>
    <div class="v-table-foot">
      <!--<foreground-pagination :data-source-len="dataSourceLength"  @getRenderList="receiveRenderList" v-if="pagination"></foreground-pagination>-->
      <pagination :data-source-len="dataSourceLength"  @getRenderData="receiveRenderData"></pagination>
    </div>
  </div>
</template>

<script>
import pagination from './pagination/Pagination'
export default {
  name: 'VTable',
  data () {
    return {
      renderList: [],
      scrollWidth: 0
    }
  },
  props: {
    columns: {
      type: Array,
      default () {
        return []
      }
    },
    dataSourceLength: {
      type: Number,
      default: 1
    },
    tableBodyHeight: {
      type: String,
      default: '10em'
    }
  },
  components: {
    pagination
  },
  mounted () {
  },
  updated () {
    this.setScrollWidth()
    let self = this
    window.addEventListener('resize', self.nextTickScrollWidth)
  },
  destroy () {
    let self = this
    window.removeEventListener('resize', self.nextTickScrollWidth)
  },
  computed: {
    paddingRight () {
      return this.scrollWidth + 'px'
    }
  },
  methods: {
    stopEvent (e) {
      let _this = document.getElementById('ioio')
      e.stopPropagation()
      _this.scrollTop += e.wheelDelta > 0 ? -60 : 60
      return false
    },
    setScrollWidth () {
      if (this.$refs.scroll) {
        this.scrollWidth = this.$refs.scroll.offsetWidth - this.$refs.scroll.clientWidth
      }
    },
    nextTickScrollWidth () {
      this.$nextTick(function () {
        this.setScrollWidth()
      })
    },
    receiveRenderData (pagelist, currentpage, pageSize) {
      this.$emit('getRenderData', pagelist, currentpage, pageSize)
    }
  }
}
</script>

<style src="./vTable.less" lang="less"></style>



