<template>
  <div class="post-body">
    <v-markdown :source="markdownText"></v-markdown>
    <component v-if="demo" :is="demo"></component>
  </div>
</template>

<script>
import demoMixin from '../../demos'

export default {
  name: 'post',
  mixins: [demoMixin],
  props: {
    contentUrl: {
      required: false,
      type: String,
      default: ''
    },
    contentTimestamp: {
      required: false,
      type: String,
      default: ''
    },
    demo: {
      required: false,
      type: String,
      default: ''
    }
  },
  data () {
    return {
      markdownText: ''
    }
  },
  mounted () {
    this.fetchData()
  },
  computed: {
  },
  methods: {
    fetchData () {
      if (this.contentUrl) {
        this.$service.cache.static(this, this.contentUrl, {}, false, this.contentTimestamp).then(data => {
          this.markdownText = data
        })
      }
      this.markdownText = ''
    }
  },
  watch: {
    contentUrl () {
      this.fetchData()
    }
  }
}
</script>

<style src="./post.less" lang="less"></style>



