import Confirm from './components/vueConfirm/VueConfirm'

var domState = false
var appendComponent = function (el) {
  if (!domState) {
    document.getElementsByTagName('body')[0].appendChild(el)
    domState = true
  }
}

export default {
  install: function (Vue, opt) {
    if (opt.store) {
      Confirm.store = opt.store
    }
    var GlobalConfirmComponent = Vue.extend(Confirm)
    var globalConfirm = new GlobalConfirmComponent().$mount()

    appendComponent(globalConfirm.$el)

    if (opt && typeof opt === 'object') {
      var attrs = ['opacity', 'backdrop', 'color', 'confirmText', 'cancelText']
      for (var i = 0; i < attrs.length; i++) {
        if (opt[attrs[i]]) {
          globalConfirm[attrs[i]] = opt[attrs[i]]
          globalConfirm.defOption[attrs[i]] = opt[attrs[i]]
        }
      }
    }

    Vue.prototype.$confirm = {
      show (opt) {
        return globalConfirm.show(opt)
      },
      hideAll () {
        globalConfirm.hideAll()
      }
    }
  }
}



