import Hello from './components/vueMessage/VueMessage'

var domState = false
var appendComponent = function (el) {
  if (!domState) {
    document.getElementsByTagName('body')[0].appendChild(el)
    domState = true
  }
}
var showMessage = function (component, type, config) {
  component.show(config, type)
}

export default {
  install: function (Vue, opt) {
    var GlobalMessageComponent = Vue.extend(Hello)
    var globalMessage = new GlobalMessageComponent().$mount()

    if (opt && typeof opt.duration === 'number') {
      globalMessage.duration = opt.duration
      globalMessage.defOption.duration = opt.duration
    }

    appendComponent(globalMessage.$el)

    Vue.prototype.$message = {
      info: function (config) {
        showMessage(globalMessage, 'info', config)
      },
      success: function (config) {
        showMessage(globalMessage, 'success', config)
      },
      error: function (config) {
        showMessage(globalMessage, 'error', config)
      },
      warning: function (config) {
        showMessage(globalMessage, 'warning', config)
      },
      loading: function (config) {
        showMessage(globalMessage, 'loading', config)
        return globalMessage.hide
      }
    }
  }
}



