<template>
  <div class="vue-notice-item-container" :class="[{ 'show': showState, 'transition': animate, 'no-msg': !noticeMsg && !link }, typeClassName]" :style="containerStyle">
    <a class="vue-notice-item-close-btn" @click="hideNotice">
      <i class="icon-remove"></i>
    </a>
    <div class="vue-notice-item-icon" v-if="noticeType !== 'open'">
      <i :class="classMap[noticeType]"></i>
    </div>
    <div class="vue-notice-item-content">
      <div class="vue-notice-title">{{noticeTitle}}</div>
      <div class="vue-notice-msg">
        <div v-if="noticeMsg">
          {{noticeMsg}}
        </div>
        <div v-if="link" class="vue-notice-link-container">
          <a @click="clickLink">{{ link.label }}</a>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    noticeType: {
      required: false,
      type: String,
      default: 'open'
    },
    noticeTitle: {
      required: false,
      type: String,
      default: ''
    },
    noticeMsg: {
      required: false,
      type: String,
      default: ''
    },
    duration: {
      required: false,
      type: Number,
      default: 1500
    },
    onClose: {
      required: false,
      default: null
    },
    transition: {
      required: false,
      type: Number,
      default: this.defaultTransition
    },
    link: {
      required: false,
      type: Object,
      default: null
    }
  },
  data () {
    return {
      showState: false,
      closeTimer: null,
      removeTimer: null,
      defaultTransition: 300,
      classMap: {
        'info': ['icon-info-sign'],
        'success': ['icon-ok-sign'],
        'warning': ['icon-exclamation-circle'],
        'error': ['icon-remove-sign']
      }
    }
  },
  computed: {
    typeClassName () {
      return this.classMap[this.noticeType] ? this.noticeType : ''
    },
    animate () {
      return this.transition > 0
    },
    containerStyle () {
      let _style = {}
      if (this.animate && this.transition !== this.defaultTransition) {
        _style = {
          'transition': `transform linear ${this.transition}ms`
        }
      }
      return _style
    }
  },
  mounted () {
    setTimeout(this.showNotice, 100)
  },
  destroyed () {
    this.clearRemoveTimer()
  },
  methods: {
    showNotice () {
      this.showState = true
      this.setCloseTimer()
    },
    hideNotice () {
      this.showState = false
      this.clearCloseTimer()
      this.setRemoveTimer()
      this.closeCallback()
    },
    removeNotice () {
      this.$emit('remove')
    },
    closeCallback () {
      if (typeof this.onClose === 'function') {
        this.onClose()
      }
    },
    setCloseTimer () {
      this.clearCloseTimer()
      if (this.duration > 0) {
        this.closeTimer = setTimeout(this.hideNotice, this.duration)
      }
    },
    clearCloseTimer () {
      if (this.closeTimer) {
        clearTimeout(this.closeTimer)
        this.closeTimer = null
      }
    },
    setRemoveTimer () {
      this.clearRemoveTimer()
      if (this.transition > 0) {
        this.removeTimer = setTimeout(this.removeNotice, this.transition + 200)
      }
    },
    clearRemoveTimer () {
      if (this.removeTimer) {
        clearTimeout(this.removeTimer)
        this.removeTimer = null
      }
    },
    clickLink () {
      if (this.link && typeof this.link.action === 'function') {
        this.link.action(this.hideNotice)
      }
    }
  },
  components: {},
  events: {
  }
}
</script>
<style src="./notice.less" lang="less"></style>



