<template>
  <label class="v-radio-container" :class="radioClasses">
    <input class="hidden-input v-radio-input" type="radio" :name="name" :value="value" v-model="model" :disabled="disabled">
    <div class="v-radio-fake-border"></div>
    <div class="v-radio-handler"></div><div class="v-radio-label"><slot></slot></div>
  </label>
</template>

<script>
export default {
  model: {
    prop: 'picked',
    event: 'change'
  },
  props: {
    picked: String,
    type: {
      required: false,
      type: String,
      default: 'default'
    },
    shape: {
      required: false,
      type: String,
      default: 'default'
    },
    size: {
      required: false,
      type: String,
      default: 'default'
    },
    name: {
      required: true,
      type: String
    },
    value: {
      required: true
    },
    disabled: {
      required: false,
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      typeMap: {
        default: '',
        button: 'button'
      },
      shapeMap: {
        default: '',
        circle: 'circle'
      },
      sizeMap: {
        large: 'large',
        default: 'default',
        small: 'small',
        mini: 'mini'
      },
      attrs: [
        {
          map: 'typeMap',
          prop: 'type',
          defaultClass: ''
        },
        {
          map: 'shapeMap',
          prop: 'shape',
          defaultClass: ''
        },
        {
          map: 'sizeMap',
          prop: 'size',
          defaultClass: 'default'
        }
      ],
      disabledClassName: 'disabled',
      pickedClassName: 'picked'
    }
  },
  computed: {
    propsClasses () {
      let classes = []
      for (let attr of this.attrs) {
        let className = this[attr.map][this[attr.prop]] || attr.defaultClass
        if (className) {
          classes.push(className)
        }
      }
      return this.disabled ? classes.concat([this.disabledClassName]) : classes
    },
    radioClasses () {
      return this.model === this.value ? this.propsClasses.concat([this.pickedClassName]) : this.propsClasses
    },
    model: {
      set (val) {
        this.$emit('change', val)
      },
      get () {
        return this.picked
      }
    }
  }
}
</script>
<style src="./vRadio.less" lang="less"></style>



