<template>
  <div :class="wrapClasses">
    <div class="v-input-number-handler-wrap">
      <a  @click="up"
          @mousedown="preventDefault"
          :class="upClasses">
          <span class="v-input-number-handler-up-inner icon-angle-up" @click="preventDefault"></span>
      </a>
      <a  @click="down"
          @mousedown="preventDefault"
          :class="downClasses">
          <span class="v-input-number-handler-down-inner icon-angle-down" @click="preventDefault"></span>
      </a>
    </div>
    <div class="v-input-number-input-wrap">
      <input  class="v-input-number-input"
              :disabled="disabled"
              autocomplete="off"
              @focus="focus"
              @blur="blur"
              @keydown.stop="keyDown"
              @change="change"
              :name="name"
              :value="currentValue">
    </div>
</div>
</template>
<script>
import { oneOf } from '../../lib/utils'
export default {
  props: {
    max: {
      type: Number,
      default: Infinity
    },
    min: {
      type: Number,
      default: -Infinity
    },
    step: {
      type: Number,
      default: 1
    },
    value: {
      type: Number,
      default: 1
    },
    size: {
      validator (value) {
        return oneOf(value, ['small', 'large', 'default'])
      }
    },
    disabled: {
      type: Boolean,
      default: false
    },
    name: {
      type: String
    }
  },
  data () {
    return {
      focused: false,
      upDisabled: false,
      downDisabled: false,
      currentValue: this.value
    }
  },
  mounted () {
    this.changeVal(this.currentValue)
  },
  computed: {
    wrapClasses () {
      return [
        'v-input-number',
        {
          [`v-input-number-${this.size}`]: !!this.size,
          'v-input-number-disabled': this.disabled,
          'v-input-number-focused': this.focused
        }
      ]
    },
    upClasses () {
      return [
        'v-input-number-handler',
        'v-input-number-handler-up',
        {
          'v-input-number-handler-up-disabled': this.upDisabled
        }
      ]
    },
    downClasses () {
      return [
        'v-input-number-handler',
        'v-input-number-handler-down',
        {
          'v-input-number-handler-down-disabled': this.downDisabled
        }
      ]
    }
  },
  methods: {
    preventDefault (e) {
      e.preventDefault()
    },
    up (e) {
      const targetVal = Number(e.target.value)
      if (this.upDisabled && isNaN(targetVal)) {
        return false
      }
      this.changeStep('up', e)
    },
    down (e) {
      const targetVal = Number(e.target.value)
      if (this.downDisabled && isNaN(targetVal)) {
        return false
      }
      this.changeStep('down', e)
    },
    changeStep (type, e) {
      if (this.disabled) {
        return false
      }
      const targetVal = Number(e.target.value)
      let val = Number(this.currentValue)
      const step = Number(this.step)
      if (isNaN(val)) {
        return false
      }
      // input a number, and key up or down
      if (!isNaN(targetVal)) {
        if (type === 'up') {
          if (this.addNum(targetVal, step) <= this.max) {
            val = targetVal
          } else {
            return false
          }
        } else if (type === 'down') {
          if (this.addNum(targetVal, -step) >= this.min) {
            val = targetVal
          } else {
            return false
          }
        }
      }
      if (type === 'up') {
        val = this.addNum(val, step)
      } else if (type === 'down') {
        val = this.addNum(val, -step)
      }
      this.setValue(val)
    },
    setValue (val) {
      this.$nextTick(() => {
        this.currentValue = val
        this.$emit('input', val)
        this.$emit('on-change', val)
      })
    },
    focus () {
      this.focused = true
    },
    blur () {
      this.focused = false
    },
    keyDown (e) {
      if (e.keyCode === 38) {
        e.preventDefault()
        this.up(e)
      } else if (e.keyCode === 40) {
        e.preventDefault()
        this.down(e)
      }
    },
    change (event) {
      let val = event.target.value.trim()

      const max = this.max
      const min = this.min

      if (this.isValueNumber(val)) {
        val = Number(val)
        this.currentValue = val

        if (val > max) {
          this.setValue(max)
        } else if (val < min) {
          this.setValue(min)
        } else {
          this.setValue(val)
        }
      } else {
        event.target.value = this.currentValue
      }
    },
    changeVal (val) {
      if (this.isValueNumber(val) || val === 0) {
        val = Number(val)
        const step = this.step
        this.upDisabled = val + step > this.max
        this.downDisabled = val - step < this.min
      } else {
        this.upDisabled = true
        this.downDisabled = true
      }
    },
    isValueNumber (value) {
      return (/(^-?[0-9]+\.{1}\d+$)|(^-?[1-9][0-9]*$)|(^-?0{1}$)/).test(value + '')
    },
    addNum (num1, num2) {
      let sq1, sq2, m
      try {
        sq1 = num1.toString().split('.')[1].length
      } catch (e) {
        sq1 = 0
      }
      try {
        sq2 = num2.toString().split('.')[1].length
      } catch (e) {
        sq2 = 0
      }
      m = Math.pow(10, Math.max(sq1, sq2))
      return (Math.round(num1 * m) + Math.round(num2 * m)) / m
    }
  },
  watch: {
    value (val) {
      this.currentValue = val
    },
    currentValue (val) {
      this.changeVal(val)
    },
    min () {
      this.changeVal(this.currentValue)
    },
    max () {
      this.changeVal(this.currentValue)
    }
  }
}
</script>

<style src="./vInputNumber.less" lang="less"></style>



