<template>
  <div class="placeholder-container" :class="{ 'placeholder-load-fail': exactType === 'error' || exactType === 'noData'}">
    <div class="loadFail-container-text">
      <svg-icon :icon-name="iconType" :theme-separator="themeSeparator" class='loading-img'></svg-icon>
      <div class='loading-text' >
        <span>{{ msgTxt }}</span>
        <loading-icon v-if="exactType === 'loading'"></loading-icon>
      </div>
    </div>
    <slot v-if="exactType === 'error'"></slot>
  </div>
</template>
<script>
export default {
  props: {
    type: {
      validator (value) {
        let _type = value.split('.')[0]
        return ['loading', 'error', 'noData'].indexOf(_type) !== -1
      },
      default: 'loading'
    },
    msg: {
      type: String,
      default: ''
    }
  },
  data () {
    return {
      defaultMsg: {
        loading: this.$t('text.loadingMsg'),
        error: this.$t('text.errorMsg'),
        noData: this.$t('text.noDataMsg')
      },
      themeSeparator: '.',
      iconMap: {
        loading: 'loading',
        error: 'error',
        noData: 'error'
      }
    }
  },
  computed: {
    exactType () {
      return this.type.split(this.themeSeparator)[0]
    },
    msgTxt () {
      return this.msg || this.defaultMsg[this.exactType] || ''
    },
    iconType () {
      var arr = this.type.split(this.themeSeparator)
      if (arr.length > 1) {
        return this.iconMap[this.exactType] + this.themeSeparator + arr[1]
      }
      return this.iconMap[this.exactType]
    }
  }
}
</script>

<style src="./placeholder.less" lang="less"></style>



