<template>
  <div class="v-code-container">
    <pre :class="{ 'bg': bg }">
      <code :class="language" ref="code">
        <slot></slot>
      </code>
    </pre>
    <span class="copy" @click="clip">
      <i class="icon-file-text-o" v-show="!copied"></i>
      <i class="icon-clipboard" v-show="copied" style="color:#5cb85c"></i>
    </span>
  </div>
</template>

<script>
import hljs from 'hljs'
import Clipboard from 'clipboard'

export default {
  props: {
    lang: {
      requried: false,
      type: String,
      default: 'javascript'
    },
    bg: {
      requried: false,
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      code: '',
      clipCode: '',
      copied: false
    }
  },
  computed: {
    language () {
      if (this.lang === 'auto') {
        return ''
      } else {
        return this.lang
      }
    }
  },
  mounted () {
    this.code = this.$refs.code.innerHTML.replace(/\n/, '')
    this.$refs.code.innerHTML = this.code
    hljs.highlightBlock(this.$refs.code)
    this.clipCode = this.code.replace(/&lt;/g, '<').replace(/&gt;/g, '>').replace(/&amp;/g, '&')
  },
  methods: {
    clip () {
      const code = this.clipCode
      const clipboard = new Clipboard('.copy', {
        text () {
          return code
        }
      })
      clipboard.on('success', (e) => {
        e.clearSelection()
        clipboard.destroy()
        this.copied = true
        setTimeout(() => {
          this.copied = false
        }, 2000)
      })
    }
  }
}
</script>
<style src="./vCode.less" lang="less"></style>



