import Mask from './components/vuePageMask/VuePageMask'

var domState = false
var appendComponent = function (el) {
  if (!domState) {
    document.getElementsByTagName('body')[0].appendChild(el)
    domState = true
  }
}

export default {
  install: function (Vue, opt) {
    var GlobalMaskComponent = Vue.extend(Mask)
    var globalMask = new GlobalMaskComponent().$mount()

    appendComponent(globalMask.$el)

    if (opt && typeof opt === 'object') {
      var attrs = ['top', 'left', 'right', 'bottom', 'opacity', 'showLoading', 'color']
      for (var i = 0; i < attrs.length; i++) {
        globalMask[attrs[i]] = opt[attrs[i]] || globalMask.defOption[attrs[i]]
        globalMask.defOption[attrs[i]] = opt[attrs[i]] || globalMask.defOption[attrs[i]]
      }
    }

    Vue.prototype.$mask = {
      show (opt) {
        return globalMask.show(opt)
      },
      hideAll () {
        globalMask.hideAll()
      },
      play () {
        globalMask.play()
      },
      pause () {
        globalMask.pause()
      }
    }
  }
}



