<template>
  <div class="vue-notice-container" :class="{ 'reverse': reverse }">
    <notice v-for="(notice, index) in noticeList" :key="notice.timestamp" :notice-type="notice.type" :notice-title="notice.title" :notice-msg="notice.msg" :link="notice.link" :duration="notice.duration" :on-close="notice.onClose" :transition="transitionDuration" @remove="removeNotice(index)"></notice>
  </div>
</template>

<script>
import notice from './notice/notice'
export default {
  data () {
    return {
      reverse: false,
      transitionDuration: 300,
      noticeList: [],
      defOption: {
        type: 'info',
        title: '',
        msg: '',
        duration: 3000,
        onClose: null,
        link: null
      }
    }
  },
  computed: {
  },
  methods: {
    getNewNotice (config) {
      let _newConfig = {}
      for (let key in this.defOption) {
        _newConfig[key] = (config[key] || config[key] === 0) ? config[key] : this.defOption[key]
      }
      _newConfig.timestamp = +new Date()
      return _newConfig
    },
    addNotice (config) {
      this.noticeList.push(this.getNewNotice(config))
    },
    removeNotice (index) {
      this.noticeList.splice(index, 1)
    }
  },
  components: {
    notice
  },
  ready () {
  },
  events: {
  }
}
</script>
<style src="./vueNotice.less" lang="less"></style>



