<template>
  <div class="scroll-mask-container" :class="[scrollDirection ? 'vertical': 'horizontal']" :style="containerStyle">
    <div class="scroll-content" :class="containerClass" :style="scrollContentStyle">
      <slot></slot>
    </div>
    <div class="mask-grad-top"></div>
    <div class="mask-grad-bottom"></div>
  </div>
</template>

<script>
export default {
  props: {
    scrollDirection: {
      required: false,
      type: Boolean,
      default: true
    },
    containerClass: {
      required: false,
      type: Array,
      default () {
        return []
      }
    },
    maxLength: {
      required: false,
      type: String,
      default: 'auto'
    }
  },
  computed: {
    scrollContentStyle () {
      let key = this.scrollDirection ? 'max-height' : 'max-width'
      let _result = {}
      if (this.maxLength !== 'auto') {
        _result[key] = this.maxLength
      }
      return _result
    },
    containerStyle () {
      let style = {}
      if (!this.scrollDirection) {
        style['max-width'] = this.maxLength
      }
      return style
    }
  }
}
</script>
<style src="./scrollMask.less" lang="less"></style>



