<template>
  <label class="v-switch-container" :class="switchClasses" @click="switchClick">
    <input class="hidden-input v-switch-input" type="checkbox" v-model="model" :disabled="disabled">
    <div class="v-switch-handler">
      <div class="v-switch-label v-switch-close"><slot name="close"></slot></div>
      <div class="v-switch-label v-switch-open"><slot name="open"></slot></div>
    </div>
  </label>
</template>

<script>
export default {
  model: {
    prop: 'checked',
    event: 'change'
  },
  props: {
    checked: Boolean,
    size: {
      required: false,
      type: String,
      default: 'default'
    },
    disabled: {
      required: false,
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      sizeMap: {
        large: 'large',
        default: 'default',
        small: 'small',
        mini: 'mini'
      },
      attrs: [
        {
          map: 'sizeMap',
          prop: 'size',
          defaultClass: 'default'
        }
      ],
      disabledClassName: 'disabled',
      pickedClassName: 'checked'
    }
  },
  computed: {
    propsClasses () {
      let classes = []
      for (let attr of this.attrs) {
        let className = this[attr.map][this[attr.prop]] || attr.defaultClass
        if (className) {
          classes.push(className)
        }
      }
      return classes
    },
    switchClasses () {
      return this.propsClasses.concat([{
        [this.disabledClassName]: this.disabled,
        [this.pickedClassName]: this.model
      }])
    },
    model: {
      set (val) {
        this.$emit('change', val)
        this.onChange(val)
      },
      get () {
        return this.checked
      }
    }
  },
  methods: {
    switchClick (event) {
      if (this.disabled) {
        event.stopPropagation()
        event.preventDefault()
        return false
      }
    },
    onChange (val) {
      this.$emit('on-change', val)
    }
  }
}
</script>
<style src="./vSwitch.less" lang="less"></style>



