<template>
  <div class="v-transfer-container">
    <div class="v-transfer-list-outer">
      <div class="v-transfer-list-container">
        <div class="v-transfer-list-header">
          {{ $t('text.sourceList') }}
        </div>
        <div v-if="filterable" class="v-transfer-filter">
          <input class="v-input" v-model="sourceQuery" :placeholder="$t('text.searchPlaceholder')" />
        </div>
        <scroll-mask class="v-transfer-list-body source" :container-class="['v-transfer-list']">
          <div v-for="item in sourceRanderList" :key="item[keyName]" class="v-transfer-list-item" :class="{ 'hidden': itemState(item, sourceQuery) }" @click="clickItem(item, 'source')">
            {{ renderFormater(item) }}
          </div>
        </scroll-mask>
        <!-- <mask-wrap class="v-transfer-list-body source v-transfer-list-mask">
          <div v-for="item in sourceRanderList" :key="item[keyName]" class="v-transfer-list-item" :class="{ 'hidden': itemState(item, sourceQuery) }" @click="clickItem(item, 'source')">
            {{ renderFormater(item) }}
          </div>
        </mask-wrap> -->
      </div>
    </div>
    <div class="v-transfer-list-outer">
      <div class="v-transfer-list-container">
        <div class="v-transfer-list-header">
          {{ $t('text.targetList') }}
        </div>
        <div v-if="filterable" class="v-transfer-filter">
          <input class="v-input" v-model="targetQuery" :placeholder="$t('text.searchPlaceholder')" />
        </div>
        <scroll-mask class="v-transfer-list-body target" :container-class="['v-transfer-list']">
          <div v-for="item in model" :key="item[keyName]" class="v-transfer-list-item" :class="{ 'hidden': itemState(item, targetQuery) }" @click="clickItem(item, 'target')">
            {{ renderFormater(item) }}
          </div>
        </scroll-mask>
        <!-- <mask-wrap class="v-transfer-list-body target v-transfer-list-mask">
          <div v-for="item in model" :key="item[keyName]" class="v-transfer-list-item" :class="{ 'hidden': itemState(item, targetQuery) }" @click="clickItem(item, 'target')">
            {{ renderFormater(item) }}
          </div>
        </mask-wrap> -->
      </div>
    </div>
  </div>
</template>

<script>
export default {
  model: {
    prop: 'target',
    event: 'change'
  },
  props: {
    keyName: {
      required: false,
      type: String,
      default: 'key'
    },
    target: {
      required: false,
      type: Array,
      default () {
        return []
      }
    },
    source: {
      required: false,
      type: Array,
      default () {
        return []
      }
    },
    renderFormater: {
      required: false,
      type: Function,
      default (item) {
        return item.label
      }
    },
    filterable: {
      required: false,
      type: Boolean,
      default: true
    },
    filterMethod: {
      required: false,
      type: Function,
      default (item, query) {
        return item.label.indexOf(query) !== -1
      }
    }
  },
  data () {
    return {
      sourceQuery: '',
      targetQuery: ''
    }
  },
  created () {
  },
  computed: {
    model: {
      set (val) {
        this.$emit('change', val)
        this.onChange(val)
      },
      get () {
        return this.target
      }
    },
    sourceRanderList () {
      return this.source.filter(item => !this.hasKeyValue(item[this.keyName], this.model))
    }
  },
  methods: {
    hasKeyValue (keyValue, attr) {
      return attr.some(item => {
        return item[this.keyName] === keyValue
      })
    },
    itemState (item, query) {
      return !this.filterMethod(item, query)
    },
    clickItem (item, listName) {
      if (listName === 'source') {
        this.model.push(item)
      } else {
        this.model.splice(this.model.indexOf(item), 1)
      }
      this.onChange(this.model)
    },
    onChange (val) {
      this.$emit('on-change', val)
    }
  }
}
</script>
<style src="./transfer.less" lang="less"></style>



